<template>
  <div class="formulario">

    <h3 class="formulario_titulo">{{tittle}}</h3>

    <input type="text" class="formulario_input" v-model="inputValue">

    <button 
    :disabled="inputValue.length === 0"
    @click="onSubmit"
    >{{ button }}</button>

  </div>
</template>

<script>
/* eslint-disable */

export default {
  name: "Formulario",
  props:{
    tittle: String,
    button: String,
    action: Function
  },
  data(){
    return{
        inputValue: ""
    }
  },
  methods:{
    onSubmit: function(){
        this.action(this.inputValue)
        this.inputValue = ""
    }
  }
};
</script>

