.formulario{
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

input{
    padding: 10px;
    color: var(--color-text-dark);
    border-radius: 5px;
    width: 100%;
    font-size: 16px;
}

